  /* Profile container */
  .profile {
    margin: 20px 0;
    display:flex;
    justify-content:center;
  }
  
  /* Profile sidebar */
  .profile-sidebar {
    padding: 20px 0 10px 0;
    border-style: dotted;
    border-color: white;
    background-color: #3f51b5 !important;
  }
  .profile-userpic{
    display: flex;
    justify-content: center;
  }
  .profile-userpic img {
    float: none;
    margin: 0 auto;
    width: 200px;
    height: 200px;
    -webkit-border-radius: 50% !important;
    -moz-border-radius: 50% !important;
    border-radius: 50% !important;
    border-color: white;
    border-width: 5px;
  }
  
  .profile-usertitle {
    text-align: center;
    margin-top: 20px;
  }
  
  .profile-usertitle-name {
    color: white;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 7px;
  }
  
  .profile-usertitle-job {
    text-transform: uppercase;
    color: white;
    font-size: 12px;
    font-weight: 600;
    margin-bottom: 15px;
  }
  
  .profile-userbuttons {
    text-align: center;
    margin-top: 10px;
  }
  
  .profile-userbuttons .btn {
    text-transform: uppercase;
    font-size: 11px;
    font-weight: 600;
    padding: 6px 15px;
    margin-right: 5px;
  }
  
  .profile-userbuttons .btn:last-child {
    margin-right: 0px;
  }
      
  .profile-usermenu {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    overflow: auto;
  }
  
  .profile-usermenu ul li {
    border-bottom: 1px solid #f0f4f7;
    padding: 5px;
  }
  
  .profile-usermenu ul li:last-child {
    border-bottom: none;
  }
  
  .profile-usermenu ul li div {
    color: #93a3b5;
    font-size: 14px;
    font-weight: 400;
    padding: 10px;
  }
  
  .profile-usermenu ul li div i {
    margin-right: 8px;
    font-size: 14px;
  }
  
  .profile-usermenu ul li div:hover {
    background-color: #fafcfd;
    color: #5b9bd1;
  }
  

  .profile-usermenu ul li.active {
    border-bottom: none;
  }
  
  .profile-usermenu ul li.active div {
    color: #5b9bd1;
    background-color: #f6f9fb;
    border-left: 2px solid #5b9bd1;
    margin-left: -2px;
  }
  
  /* Profile Content */
  .profile-content {
    padding: 20px;
    background: #fff;
    min-height: 460px;
  }
